`, "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/backoffice/user/userList.vue"],"names":[],"mappings":";AAyUI;;QAEI,cAAc;QACd,eAAe;QACf,iBAAiB;AACrB;AACA;;QAEI,cAAc;QACd,kBAAkB;QAClB,yBAAyB;QACzB,gBAAgB;QAChB,yBAAyB;QACzB,0CAA0C;QAC1C,eAAe;AACnB;AACA;;QAEI,cAAc;QACd,kBAAkB;QAClB,iBAAiB;AACrB;AACA;;QAEI,yBAAyB;AAC7B;AACA;;QAEI,eAAe;QACf,cAAc;AAClB","sourcesContent":["<script setup lang=\"ts\">\n    import { computed, reactive, ref, watch, watchEffect } from \"vue\";\n    import { IUser_SUSI } from \"../../../../../../../common/qCloudTemp/authentication\";\n    import { container } from \"../../../../../domain/core/diContainer\";\n    import { IRouter } from \"../../../../../domain/presentation/IRouter\";\n    import CustomSelect from \"../../customSelect.vue\"\n\n    interface IUserListProps {\n        users: Array<IUser_SUSI>;\n    }\n    \n    const router = container.resolve<IRouter>(IRouter);\n    const props = defineProps<IUserListProps>();\n    const users = props.users;\n    const searchUser = ref(\"\");\n\n    const currentPage = ref(1);\n    const paginatedData = ref<Array<IUser_SUSI>>([]);\n    const itemsPerPageOptions = ref(['10', '20', '30', 'All']);\n    const selectedItemsPerPage = ref('10');\n    const filterOptions = ref(['All', 'Active', 'Moderate', 'Non-Active']);\n    const selectedFilter = ref<Array<number>>([0,10000]);\n    function viewUser(userId: string){\n        router.push(`/backoffice/user/${userId}`);\n    }\n    // Activity gelene kadar test için yapıldı.\n    users.forEach((user,index) => {\n        if(index < 10) {\n            user.activity = 0;\n        }\n        else if(index >= 10 && index < 20) {\n            user.activity = 1;\n        }\n        else if(index >=20 && index < 36 ) {\n            user.activity = 4\n        }\n        else {\n            user.activity = 2\n        }\n    })\n    ///////////////////////////////////////////////////\n    function getUserList() {\n        return users.filter((user: IUser_SUSI) => (user.name?.toUpperCase().includes(searchUser.value.toUpperCase()) || \n                                                user.lastName?.toUpperCase().includes(searchUser.value.toUpperCase()) || \n                                                user.email?.toUpperCase().includes(searchUser.value.toUpperCase())) \n                                                && user.activity! >= selectedFilter.value[0] && user.activity! <= selectedFilter.value[1]);\n        // if(selectedFilter.value == 'All') {\n        //     return result;\n        // }\n        // else {\n        //     return result.filter((user:IUser_SUSI) => user.role == selectedFilter.value);\n        // }\n    }\n\n\n    const totalPages = computed(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            return Math.ceil(getUserList().length / parseInt(selectedItemsPerPage.value))\n        }\n        else {\n            return 1;\n        }\n    });\n\n    const displayedPageNumbers = computed(() => {\n        const range = 2; // Number of page numbers to display on each side of the current page\n        let startPage = Math.max(1, currentPage.value - range);\n        let endPage = Math.min(totalPages.value, currentPage.value + range);\n        if (endPage - startPage < 2 * range) {\n            // Adjust page numbers to always show the same number of items\n            if (startPage === 1) {\n                endPage = Math.min(endPage + (2 * range - (endPage - startPage)), totalPages.value);\n            } \n            else if (endPage === totalPages.value) {\n                startPage = Math.max(startPage - (2 * range - (endPage - startPage)), 1);\n            }\n        }\n        return Array.from({ length: endPage - startPage + 1 }, (_, index) => startPage + index);\n    });\n\n    watchEffect(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            const startIndex = (currentPage.value - 1) * parseInt(selectedItemsPerPage.value);\n            const endIndex = startIndex + parseInt(selectedItemsPerPage.value);\n            const newData = getUserList().slice(startIndex, endIndex);\n            paginatedData.value = newData;\n        }\n        else {\n            paginatedData.value = getUserList();\n        }\n    });\n\n    function onItemsPerPageSelect (itemCount: string) {\n        selectedItemsPerPage.value = itemCount;\n        setCurrentPage(1)\n    }\n    function setCurrentPage(page: number) {\n        currentPage.value = page;\n    }\n\n    watchEffect(() => {\n        currentPage.value = 1;\n    });\n    function filterTable(filterType: string) {\n        switch(filterType) {\n            case 'All' : selectedFilter.value = [0,10000]; break;\n            case 'Active': selectedFilter.value = [4,10000]; break;\n            case 'Moderate': selectedFilter.value = [1,3]; break;\n            case 'Non-Active': selectedFilter.value = [0,0]; break;\n        }\n    }\n</script>\n\n<template>\n    <div class=\"page-title\">\n        <span>User List ({{ getUserList().length }})</span>\n    </div>\n    <div class=\"filter-area\">\n        <div class=\"search-in-table\">\n            <i class=\"mdi mdi-magnify\"></i>\n            <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"searchUser\" @input=\"setCurrentPage(1)\"/>\n        </div>\n        <div>\n            <CustomSelect :options=\"filterOptions\" :default=\"filterOptions[0]\" @input=\"filterTable\" class=\"filters\" :selected-class=\"'selected-filter'\"></CustomSelect>\n        </div>\n        <div class=\"quick-filters\">\n            <label>Quick Filters: </label>\n            <span :class=\"{active: selectedFilter[0] == 4}\" @click=\"filterTable('Active')\">Active</span>\n            <span :class=\"{active: selectedFilter[0] == 1}\" @click=\"filterTable('Moderate')\">Moderate</span>\n            <span :class=\"{active: selectedFilter[0] == 0 && selectedFilter[1] == 0}\" @click=\"filterTable('Non-Active')\">Non-Active</span>\n        </div>\n    </div>\n    <div style=\"padding-bottom: 50px;\">\n        <table class=\"users-table\">\n            <tr class=\"thead\">\n                <th class=\"col-xl-3\">ID</th>\n                <th class=\"col-xl-2\">Object ID</th>\n                <th class=\"col-xl-1\">Name</th>\n                <th class=\"col-xl-2\">Last Name</th>\n                <th class=\"col-xl-1\">E-mail</th>\n                <th class=\"col-xl-1\">Last Login</th>\n                <th class=\"col-xl-1\">Activity</th>\n                <th class=\"col-xl-1\"></th>\n            </tr>\n            <tr v-for=\"user in paginatedData\" :key=\"user.id\">\n                <td>{{ user.id }}</td>\n                <td>{{ user.objectId }}</td>\n                <td>{{ user.name }}</td>\n                <td>{{ user.lastName }}</td>\n                <td>{{ user.email }}</td>\n                <td> - </td>\n                <td>  \n                    <!-- <span style=\"background: #FF6B6B; border-radius: 50%;padding: 6px 10px;\">0</span>  -->\n                    <span style=\"border-radius: 50%;padding: 6px 10px;\" \n                    :style=\"{background: user.activity == 0 ? '#FF6B6B' : user.activity && user.activity >= 1 && user.activity <= 3 ? '#F3CA40' : user.activity && user.activity >=4 ? '#29C660' : ''}\">\n                    {{ user.activity }}</span> \n                </td>\n                <td><button class=\"view-btn\" @click=\"viewUser(user.id)\">VIEW</button></td>\n            </tr>\n            <!-- Pagination -->\n            <tr>\n                <td colspan=\"1\" style=\"display: flex; align-items: center;\">\n                    <span style=\"font-size: 14px; color: #616161\">Items per page:</span>\n                    <CustomSelect :options=\"itemsPerPageOptions\" @input=\"onItemsPerPageSelect\" class=\"items-per-page\" :selected-class=\"'item-count'\" :default=\"selectedItemsPerPage\"></CustomSelect>\n                </td>\n                <td colspan=\"7\">\n                    <div style=\"float: right; display: flex; align-items: center;\">\n                        <button class=\"change-page-number\" @click=\"currentPage--\" :disabled=\"currentPage === 1\">\n                            <i class=\"mdi mdi-chevron-left\" :style=\"{color: currentPage === 1 ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                        <button v-for=\"pageNumber in displayedPageNumbers\" :key=\"pageNumber\" @click=\"setCurrentPage(pageNumber)\" :class=\"currentPage === pageNumber ? 'page-number active' : 'page-number'\"> {{ pageNumber }} </button>\n                        <button class=\"change-page-number\" @click=\"currentPage++\" :disabled=\"currentPage === totalPages\">\n                            <i class=\"mdi mdi-chevron-right\" :style=\"{color: currentPage === totalPages ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                    </div>\n                </td>\n            </tr>\n        </table>\n\n    </div>\n</template>\n\n<style scoped>\n* {\n    font-family: \"Roboto\";\n}\n.page-title {\n    font-weight: 500;\n    font-size: 24px;\n    line-height: 28px;\n    color: #151233;\n}\n.filter-area {\n    margin: 20px 0;\n    display: flex;\n    align-items: center;\n}\n.search-in-table {\n    background: transparent;\n    border: 1px solid #BCBCBC !important;\n    border-radius: 4px !important;\n    height: 32px;\n    display: flex;\n    align-items: center;\n}\n.search-input {\n    background: transparent;\n    border: none;\n}\n.search-input:focus {\n    outline: none;\n}\n.search-in-table i {\n    color: #BCBCBC;\n    font-size: 20px;\n    padding: 0px 4px;\n}\n\n.view-btn {\n    background: none;\n    border:none;\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.users-table {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    width: 100%;\n}\ntd:hover {\n    background-color: none !important;\n}\ntable tr:hover:not(:last-child):not(:first-child) {\n    background-color: #EEEEEE;\n}\n.users-table th\n    {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 18px 36px\n}\n.users-table td {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 20px 36px;\n    color: #212121;\n    font-size: 14px;\n    line-height: 16px;\n}\n.users-table .thead {\n    background: #F5F5F5;\n    color: #616161;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.items-per-page {\n    background: #FFFFFF;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    width: 84px;\n    padding: 7px 12px;\n    margin-left: 12px\n}\n.filters {\n    width: 115px;\n    background: transparent;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 7px 12px;\n    margin-left: 20px;\n}\n.page-number {\n    padding: 8px 12px;\n    color: #616161;\n    font-size: 14px;\n    line-height: 16px;\n    background: none;\n    border: none\n}\n.page-number.active {\n    background: #151233;\n    border-radius: 4px;\n    color: #FFFFFF;\n}\n.change-page-number {\n    border: none;\n    background: none;\n}\n.change-page-number i {\n    color: #151233; \n    font-size: 20px; \n    line-height: 20px;\n}\n.quick-filters {\n    display: flex;\n    align-items: center;\n    margin-left: 20px;\n}\n.quick-filters label {\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n    color: #616161;\n    margin-right: 4px;\n}\n\n.quick-filters span {\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n    padding: 8px 16px;\n    margin-right: 4px;\n    cursor: pointer;\n}\n.quick-filters span:hover {\n    background: #D5D5D5;\n    border-radius: 4px;\n}\n.quick-filters span.active {\n    background: #2C5A86;\n    border-radius: 4px;\n    color: #FFFFFF;\n}\n</style>\n<style>\n    .item-count,\n    .selected-filter {\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .items-per-page .items,\n    .filters .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        margin-top: 8px;\n    }\n    .items-per-page .items .option,\n    .filters .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n    }\n    .items-per-page .items .option:hover,\n    .filters .items .option:hover {\n        background-color: #EEEEEE;\n    }\n    .items-per-page i,\n    .filters i {\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n</style>"],"sourceRoot":""}]);
